import React, { PureComponent } from 'react';import hoistNonReactStatics from 'hoist-non-react-statics';export default (WrapComponent) => {  class RoleComponent extends PureComponent {    constructor(props) {      super(props);      this.state = {        role: props.route.path === '/employee' ? 'employee' : 'admin'      }    }    componentWillReceiveProps(nextProps) {      if (this.props.route.path !== nextProps.route.path && nextProps.route.path !== '') {        const routing = nextProps.route.path;        if (routing === '/employee') {          this.setState({            role: 'employee'          });        } else {          this.setState({            role: 'admin'          });        }      }    }    render() {      const { role } = this.state;      return (        <WrapComponent role={role} {...this.props} />      )    }  }  return hoistNonReactStatics(RoleComponent, WrapComponent);};